/* About section styling */

.about__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
}

.about__content {
  flex: 1 1 400px;
  position: relative;
}

.about__content p {
  margin-bottom: 1rem;
  font-size: 0.95rem;
  color: var(--color-text);
}

.about__image-wrapper {
  flex: 1 1 300px;
  display: flex;
  justify-content: center;
  position: relative;
}

.about__face {
  box-shadow: 0 6px 18px var(--color-shadow);
  border: 2px solid var(--color-surface);
}

.about__face--tl { top: -12px; left: -12px; }
.about__face--br { bottom: -12px; right: -12px; }

@media (max-width: 768px) {
  .about__face--tl { top: -8px; left: -8px; transform: scale(0.9); }
  .about__face--br { bottom: -8px; right: -8px; transform: scale(0.9); }
}

.about__image {
  max-width: 100%;
  height: auto;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 12px var(--color-shadow);
}

.city-about-section {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 2px solid var(--color-border);
}

.city-about-section h3 {
  color: var(--color-primary);
  margin-bottom: 1rem;
  font-size: 1.4rem;
}

.city-about-section p {
  color: var(--color-text);
  line-height: 1.6;
  margin-bottom: 0;
}
